//forms

.form {
	.form-rules,
	.form__text {
		font-size: .75rem;
		line-height: 1.25rem;
		color: var(--color-3);
		a {
			color: inherit;
			text-decoration: underline; } }
	&_field {
		background: #FFFFFF;
		border-radius: 2px; }

	&-control {
		height: calc(1.5em + .75rem + 7px);
		padding: 0.563rem 1rem 0.5rem 0.563rem;
		font-size: inherit;
		line-height: 1.5;
		color: var(--color-3);
		&:not(.textarea):not(.inp-search) {
			border: 0; }
		&.inp-search {
			padding: 0.563rem 1rem 0.5rem 2.813rem; }
		&.textarea {
			padding-top: 1.063rem;
			padding-bottom: 1.875rem;
			height: initial; }

		textarea {
			width: 100%;
			display: block;
			margin-bottom: 10px;
			min-height: 150px; } }

	&-row {
		margin: 0 -15px;
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0; }
		.form-group {
			padding: 0 15px;
			margin-bottom: 0;
			//margin-bottom: 20px
			//&:last-child
 } }			//	margin-bottom: 0

	&-group {
		width: 100%;
		label {
			width: 100%; }
		.btn-small {
			margin-top: 12px; } }
	&__heading {
		font-size: 1rem;
		line-height: 1.188rem;
		font-weight: 600;
		color: var(--color-8);
		//margin-top: 30px
		margin-bottom: 16px; }
	&-text {
		font-size: 0.75rem;
		line-height: 0.938rem;
		color: var(--color-22); }


	/*  */
	&-quiz-radio {
		&__list {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			width: 100%; }
		&__item {
			margin-bottom: 15px;
			&:not(:last-child) {
				margin-right: 15px; } }
		&__label {
			width: 78px !important;
			height: 62px;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 5px;
			border-radius: 3px;
			cursor: pointer;
			position: relative;
			&:before {
				content: "";
				width: 20px;
				height: 20px;
				background-position: center center;
				background-repeat: no-repeat;
				position: absolute;
				top: -6px;
				right: -6px; } }
		&__input {
			display: none; } } }

.form-quiz-radio__input:checked~.form-quiz-radio__label:before {
	background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle r='9' transform='matrix(-1 0 0 1 10 10)' fill='white' stroke='%23EFF3F8' stroke-width='2'/%3E%3Cpath d='M7 10.0292L9.31052 12L14 8' stroke='%2358CD55' stroke-width='2'/%3E%3C/svg%3E%0A"); }

.form-result {
	padding-top: 30px; }

// 
.password {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	.password-show {
		background-image: url("data:image/svg+xml,%3Csvg width='19' height='13' viewBox='0 0 19 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.9435 6.46092L17.959 6.42308L17.9435 6.38523C16.6309 3.17486 13.3099 0.9 9.42546 0.9C5.54105 0.9 2.21997 3.17486 0.907437 6.38523L0.891965 6.42308L0.907437 6.46092C2.21997 9.67129 5.54105 11.9462 9.42546 11.9462C13.3099 11.9462 16.6309 9.67129 17.9435 6.46092ZM17.0441 6.42308C15.7645 9.25163 12.7864 11.1436 9.42546 11.1436C6.06449 11.1436 3.08643 9.25163 1.80683 6.42308C3.08643 3.59452 6.06449 1.70256 9.42546 1.70256C12.7864 1.70256 15.7645 3.59453 17.0441 6.42308ZM11.2698 6.42308C11.2698 7.35947 10.451 8.13077 9.42546 8.13077C8.39987 8.13077 7.58112 7.35947 7.58112 6.42308C7.58112 5.48668 8.39987 4.71538 9.42546 4.71538C10.451 4.71538 11.2698 5.48668 11.2698 6.42308ZM6.73301 6.42308C6.73301 7.81619 7.94545 8.93333 9.42546 8.93333C10.9055 8.93333 12.1179 7.81619 12.1179 6.42308C12.1179 5.02996 10.9055 3.91282 9.42546 3.91282C7.94545 3.91282 6.73301 5.02996 6.73301 6.42308Z' fill='%23737373' stroke='%23737373' stroke-width='0.2'/%3E%3C/svg%3E%0A");
		width: 17px;
		height: 11px;
		background-repeat: no-repeat;
		background-position: center center;
		margin-left: -36px;
		position: relative;
		z-index: 1;
		cursor: pointer; }
	.password-lost {
		font-size: 0.75rem;
		line-height: 0.938rem;
		color: var(--color-3);
		margin-top: 15px;
		display: block;
		text-align: right;
		width: 100%; }
	.form-control {
		padding-right: 40px; } }

/*  */
.event {
	&__list {}
	&__item {
		display: flex;
		align-items: center;
		&:not(:last-child) {
			margin-bottom: 10px; }
		&__date {
			max-width: 220px;
			margin-right: 15px; }
		&__time {
			max-width: 75px; }
		&__remove {
			cursor: pointer;
			margin-left: 10px;
			.icon-trash-basket {
				fill: var(--color-23); } } } }


.inp-search {
	background-image: url(../images/icon-search.svg);
	background-repeat: no-repeat;
	background-color: var(--color-white);
	background-position: center left 14px;

	border: 1px solid #E2E2E2;

	margin: 0 auto;

	&:focus {
 } }		//background: transparent

.header {
	.inp-search {
		max-width: 745px; } }

/* valid */
.is {
	&-valid {
		&:after {
			content: "";
			background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.46484 5.56881L6.0293 10.1333L15.2934 0.869141' stroke='%2358CD55' stroke-width='2'/%3E%3C/svg%3E%0A");
			background-repeat: no-repeat;
			background-position: center center;
			width: 14px;
			height: 10px;
			position: absolute;
			right: 0;
			top: 50%;
			transform: translate(23px, 70%); }
		&.form-control {} }

	&-invalid {} }

.file-upload {
	margin-top: 15px;
	margin-bottom: 40px !important;
	.form__heading {
		margin-bottom: 7px; }
	&__small {
		margin-bottom: 20px; }
	&__label {
		&__multi {
			background-color: var(--color-21);
			width: 100%;
			height: 95px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 0.875rem;
			line-height: 1.063rem;
			color: var(--color-3);
			cursor: pointer;
			&:before {
				content: "";
				display: inline-block;
				margin-right: 25px;
				width: 24px;
				height: 21px;
