		background-repeat: no-repeat !important;
		background-position: center !important;
		font-size: 0 !important; }
	.sidebar,
	.chat {
		display: none; }
	.content {
		padding: 0; }
	.wall {
		display: flex;
		flex-wrap: wrap;
		padding-top: 60px;
		padding-bottom: 50px; }
	.wall {
		> div {
			order: 2;
			width: 100%; }
		.title {
			padding: 0 15px; }
		> .tag {
			padding: 0 15px; }
		> .tag_filter {
			order: 1 !important;
			border-bottom: 1px solid #E0E0E0;
			//margin: 0 -15px 25px -15px
			margin-bottom: 25px;
			padding-left: 15px;
			padding-right: 15px;
			padding-bottom: 10px; }
		> .dropdown {
			padding: 0 15px; }
		.reklama_advt {
			padding: 0 10px; } }

	.navbar .show_more {
		display: none; }
	.navbar-nav {
		width: 100%;
		max-height: calc(100% - 60px); }
	.navbar.nav_main {
		margin-bottom: 0; }
	.navbar .nav-item {
		display: inline-flex; }
	.chat-box.active .chat-box__inner {
		flex-wrap: wrap; }
	.chat-box .chat-dialog {
		margin-right: 0; }
	.chat-box .chat-dialog,
	.chat-box .chat-people {
		max-width: 100%; }

	.chat-box .chat-outgoing-msg .chat-msg-date {
		text-align: left;
		padding-left: 60px; }
	.chat-box .chat-dialog-message {
		margin-top: 25px; }
	.post_create {
		display: none !important; } }


