//
.post {
	//
	&-recomended {
		margin-bottom: 80px;
		.title {
			margin-bottom: 30px; } }
	&-tag_dots {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 18px;
		.tag__box {
			margin-bottom: 0; }
		.dropdown {
			margin-bottom: 0; } }
	&__item {
		.like {
			&__user__list {
				margin-right: 20px; } } } }


// 
.post-slider {
	.post {
		&__item {
			display: flex;
			//align-items: flex-start
			background: var(--color-white);
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
			padding: 0; }
		&__title {
			font-size: 1.125rem;
			line-height: 1.688rem;
			font-weight: 600; }
		&__banner {
			padding: 0;
			flex-basis: 100%;
			max-width: 100%; }
		//min-width: max-content
		img {
			max-width: 100%;
			height: auto; }
		&__content {
			flex-basis: 100%;
			max-width: 100%;
			padding: 20px 30px 20px 15px;
			.tag__item {
				padding: 0;
				margin-right: 20px; }
			//padding-left: 17px
			.post__btns {
				> .user {
					margin-bottom: 22px;
					padding-top: 4px; } } }
		&__title {
			margin-bottom: 15px; } }
	.like__item {
		&:not(:last-child) {
			margin-right: 10px; } } }

.post__list,
.post__detail {
	.post {
		&__title {
			font-size: 1.125rem;
			line-height: 1.688rem;
			font-weight: 600; }
		//   
		&__item {
			background: var(--color-white);
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
			margin-bottom: 40px;
			&:last-child {
				margin-bottom: 0; }

			// 
			&.colored,
			.colored {
				* {
					color: var(--color-white); }
				svg,
				svg * {
						fill: var(--color-white);
						stroke: var(--color-white); } }


			//
			&.columns {
				display: flex;
				flex-wrap: wrap;
				padding: 0;
				.post__content {
					padding: 20px 0;
					display: flex;
					flex-wrap: wrap; }
				.post-info {
					margin-bottom: 50px;
					.post-info__list {}

					.post-info__item {
						display: flex;

						font-size: .875rem;
						line-height: 1.063rem;

						//column-count: 2
						//column-gap: 20px
						&:not(:last-child) {
							margin-bottom: 17px; } }
					.post-info__label {
						font-weight: 600;
						color: var(--color-8);
						max-width: 120px;
						min-width: 120px;
						width: 100%;
						word-break: break-all; }
					.post-info__value {} }
				.like__item {
					&:not(:last-child) {
						margin-right: 10px; } }
				&.columns-type-2 {
					box-shadow: none;
					.post__header {
						margin-bottom: 15px; }
					.post__body {
						padding-top: 0; }
					.post__cinema__sessions {
						margin-top: 0; }
					.post__excerpt {
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: initial;
						-webkit-box-orient: vertical;
						overflow: visible; }
					.post__cinema__sessions {
						//margin-top: 40px
						@media #{$desktop-above} {
							max-width: 280px; } }
					.post__footer {
						width: 100%; } }
				.post__header {
					flex-basis: 100%; }
				.post__banner {
					margin: 0; }
				.post_btns {
					flex-wrap: wrap;
					> .user {
						margin-bottom: 35px; } }
				.post__body {
					padding: 30px 25px 15px 30px; }
				.post__title {
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 1;
					-webkit-box-orient: vertical;
					overflow: hidden; }
				.post__excerpt {
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden; } }
			&__col {

				&__left {
					flex-basis: 347px;
					max-width: 347px; }
				&__right {
					flex-basis: calc(100% - 347px);
					max-width: calc(100% - 347px);
					.post__btns {
						flex-direction: column;
						align-items: flex-start;
						flex-wrap: wrap;
						& > .user {
							margin-bottom: 35px; } } } } }
		&__content {
			padding: 27px 30px 15px 30px; }

		&__title {
			margin-bottom: 15px; }
		&__excerpt {
			line-height: 1.5rem;
			color: var(--color-3);
			margin-bottom: 30px; }
		&__banner {
			margin: 0 -30px;
			margin-bottom: 25px; }
		&__gallery {
			margin: 0 -30px;
			margin-bottom: 20px;
			overflow: hidden;
			&__item {
				width: 100%;
				float: left;
				margin-bottom: 5px;
				&__width__1 {
					width: 453px; }
				&__width__2 {
					width: 224px; }
				&__width__3 {
					width: 215px; } } }
		&__cinema__sessions {
			margin-bottom: 35px;
			&__title {
				margin-bottom: 20px; }
			&__list {}
			&__item {
				display: flex;
				align-items: center;
				justify-content: space-between;
				flex-wrap: wrap;
				&:not(:last-child) {
					border-bottom: 1px solid #D6D6D6;
					padding-bottom: 12px;
					margin-bottom: 15px; }

				&__date {
					font-size: 0.75rem;
					line-height: 0.938rem;
					color: var(--color-7); }
				&__time {
					display: flex;
					&.is-overflow {
						flex-wrap: wrap;
						& > div {
							margin-left: 0;
							margin-top: 5px;
							&:not(:last-child) {
								margin-right: 5px; } } }
					&__item {
						margin-left: 5px;
						padding: 3px 5px;
						background: var(--color-8);
						border-radius: 2px;

						font-size: 0.75rem;
						line-height: 0.938rem;
						color: var(--color-white); } } } }
		&__quiz {
			margin-bottom: 50px;
			&__item {
				position: relative;
				margin-bottom: 30px;
				height: 30px;

				&__title {
					font-size: 0.875rem;
					line-height: 1.875rem;
					color: var(--color-7) !important;

					padding: 0 10px;

					position: relative;
					z-index: 1; }
				&__progress {
					position: absolute;
					top: 0;
					height: inherit;
					width: 100%;
					&__current {
						height: 30px;
						background-image: url("data:image/svg+xml,%3Csvg width='203' height='30' viewBox='0 0 203 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 2C0 0.89543 0.895431 0 2 0H200.639C201.875 0 202.815 1.10973 202.612 2.3288L198.279 28.3288C198.118 29.2932 197.283 30 196.306 30H2C0.895433 30 0 29.1046 0 28V2Z' fill='white'/%3E%3C/svg%3E%0A");
						background-repeat: no-repeat;
						background-position: center right;
						transition: width 0.4s;
						&:after {
							content: "";
							width: calc(100% - 6px);
							height: 30px;
							display: block;
							background: #fff; } }
					&__bg {
						display: block;
						width: 100%;
						height: inherit;
						z-index: 0;
						background: rgba(255, 255, 255, 0.35);
						border-radius: 2px;
						text-align: right;

						margin-top: -24px;
						line-height: 30px;
						padding: 0 10px; } } } }
		&__btns {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			flex-wrap: wrap; } } }

.post {
	&__social {
		margin-top: 35px;
		//margin-bottom: 90px
		.post__social__list {
			list-style: none;
			padding-left: 0;
			display: flex;
			align-items: center;
			margin-bottom: 0; }
		.post__social__item {
			&:not(:last-child) {
				margin-right: 10px; } }
		.post__social__link {
			cursor: pointer;
			display: block;
			* {
				stroke: none !important; } }
		.bookmark {
			margin-left: 10px;
			.post__social__link {
				display: flex;
				align-items: center; }
			.bookmark-value {
				font-size: 0.75rem;
				line-height: 1rem;
				color: var(--color-9);
				margin-left: 5px; } } } }

.post__detail {

	.post {
		&__item {
			box-shadow: none; }
		&__content {
			padding: 20px 0; }
		&__header {}

		&__body {}

		&__footer {}

		&__title {
			margin-bottom: 45px; }
		&__banner {
			margin-left: 0;
			margin-right: 0; }
		&__gallery {
			&__thumbs {
				padding-bottom: 25px; } }

		&__reply {
			.textarea {
				margin-top: 30px;
				margin-bottom: 40px; }
			&__actions {
				display: flex;
				justify-content: space-between;
				&__add {
					.btn {
						width: 34px;
						height: 34px;
						padding: 5px;
						display: inline-flex;
						align-items: center;
						justify-content: center; } }
				&__send {
					.btn {
						max-width: max-content; } } }
			&__link {
				font-size: 0.75rem;
				line-height: 0.938rem;
				text-decoration-line: underline;
				color: var(--color-9);
				text-decoration: underline;
				cursor: pointer; }
			&__dots {
				cursor: pointer; }
			&__bookmarks {
				cursor: pointer; }

			&__list {}
			&__item {
				display: flex;
				align-items: center;
				justify-content: space-between;
				flex-wrap: wrap;
				margin-bottom: 20px;

				&.deep {
					padding-left: 25px;
					border-left: 1px solid var(--color-6);
					padding-top: 15px; } }


			&__comment {
				margin-top: 20px;
				margin-bottom: 12px; }
			&__footer {
				display: flex;
				flex-basis: 100%;
				& > :not(:last-child) {
					margin-right: 10px; } }

			.show_more {
				margin-top: 40px; } } } }


