/*   */
.post-list {
	&__list {
		margin-top: 30px; }
	&__item {
		margin-bottom: 30px;
		&__inner {
			position: relative;

			background: #FFFFFF;
			box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);

			padding: 10px 0;
			padding-left: 20px;
			padding-right: 35px;

			font-size: .875rem;
			line-height: 1.188rem; }
		&__header {
			display: flex;
			align-items: center;
			margin-bottom: 15px; }
		&__body {
			margin-bottom: 20px; }
		&__footer {
			display: flex;
			align-items: center;
			justify-content: space-between; }
		&__status {
			font-size: 0.625rem;
			line-height: 0.75rem;
			padding: 2px 4px;
			display: inline-block;
			&.public {
				background-color: var(--color-26);
				color: var(--color-5); }
			&.discarded {
				background-color: var(--color-28);
				color: var(--color-29); }
			&.draft {
				background-color: var(--color-30);
				color: var(--color-31); } }

		&__tags {
			.tag__box {
				margin-bottom: 0; } }
		&__dots {
			position: absolute;
			top: 10px;
			right: 15px;
			cursor: pointer; }
		&__excerpt {
			font-size: .875rem;
			line-height: 1.5rem;
			font-weight: 600;
			color: var(--color-7); }
		&__user {
			.user {
				align-items: center; }
			.user-avatar {
				width: 45px;
				height: 45px; }
			.user-name {
				font-size: .75rem;
				line-height: 1rem;
				font-weight: 500;
				color: var(--color-7); } }
		&__date {
			font-size: .75rem;
			line-height: 1rem;
			color: var(--color-32); } } }



